import './styles.scss'
import Button from '../components/Button/Button';
import { Fragment } from 'react';
import Head from 'next/head';
import Router from 'next/router';
import { Link } from "react-scroll";
import React from "react";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import StyledInput from '../components/Input/Input';
import Modal from 'react-modal';

<meta name="viewport" content="::-webkit=device-width"></meta>
const Index = () => {

    const [open, setOpen] = React.useState(false);
  
    const handleClickOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false); 
    };

    const customStyles = {
      content : {
        top                   : '50%',
        left                  : '50%',
        right                 : 'auto',
        bottom                : 'auto',
        marginRight           : '-50%',
        transform             : 'translate(-50%, -50%)'
      }
    };
    var subtitle;
    const [modalIsOpen,setIsOpen] = React.useState(false);
    function openModal() {
      setIsOpen(true);
    }
  
    function afterOpenModal() {
      // references are now sync'd and can be accessed.
      subtitle.style.color = '#';   
    }
  
    function closeModal(){
      setIsOpen(false);
    }
       return (
        <Fragment>
            <Head>
                <title>JusCredit - Home</title>
            </Head>
            <main>
                <p>
                <div className="map-container"> 
                    <div className="map" />
                     <h1>Antecipe seu </h1>
                         <h1>crédito trabalhista</h1><h2>Têm um processo ganho? Receba agora</h2>       
                    <div className="buttons-container">
                         <Button  color="primary" onClick={handleClickOpen}>
        Quero Antecipar
      </Button>
   
      <Button  color="eity" onClick={handleClickOpen}>
        Quero Investir
      </Button>
      
       </div>
</div>
    <div>
      <div>
        <Button onClick={openModal} color="primary">Quero Antecipar</Button>
        <Modal
          isOpen={modalIsOpen}
          onAfterOpen={afterOpenModal}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel="Example Modal"
        >

          <h2 ref={_subtitle => (subtitle = _subtitle)}>Hello</h2>
          <button onClick={closeModal}>close</button>
          <div>Antecipe seu crédito</div>
                 
          <form>
            <input />
            <TextField
         autoFocus
         margin="dense"
         id="name"
         label="Email Address"
         type="email"
         fullWidth
       />
            <button>Cadastrar</button>
           
          </form>
        </Modal>
</div>
        <Button onClick={openModal} color="primary">Quero Investir</Button>
        <Modal
          isOpen={modalIsOpen}
          onAfterOpen={afterOpenModal}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel="Example Modal"
        >

          <h2 ref={_subtitle => (subtitle = _subtitle)}>Hello</h2>
          <button onClick={closeModal}>close</button>
          <div>Invista seu crédito</div>
                 
          <form>
            <input />
            <TextField
         autoFocus
         margin="dense"
         id="name"
         label="Email Address"
         type="email"
         fullWidth
       />
            <button>Cadastrar</button>
           
          </form>
        </Modal>
      </div>
  
  

    <div className="cursor-container">
                 <Button onClick={()=> Router.push('/')} color="third" ><img src="/cursor.svg"/>
                 <Link to="antecipar" > 
                        <a className="link">Conheça as vantagens</a>
                        </Link></Button>
                        </div>
              
                <div className="antecipar">
               <div className="products-bg" /> 
                    <div> <h1>Mais valor para quem vende<br/>
                              Simples para quem investe</h1> 
               <h2>
                   <br/>
                   Vantagens para quem vende:
               </h2>
                            <img src="/agilidade.svg"/>
                            <h2>
                            <div className="buttons-container"> 
                         <Button  color="sixty" onClick={handleClickOpen}>
        Quero Antecipar
      </Button>
      
        
</div>
                   Vantagens para quem investe:
               </h2>
                            <img src="/rentabilidade.svg"/>
                            <div className="buttons-container"> 
                            <Button  color="sixty" onClick={handleClickOpen}>
        Quero Investir
      </Button>
    </div>

               </div>  
                    <div className="funciona-container">
                        <h1>Como funciona a plataforma?</h1>
                        <h2>Conte com toda a segurança, agilidade <br/>
                           e praticidade na liberação do seu processo ganho.
                        </h2>  
                        <img src="/trabalhador.svg"/>  
                        </div>
                
                    <div className= "parceiros-container">
                         <h1>Seja um parceiro Juscredit</h1>
                            <h2>Indique créditos trabalhistas elegíveis e ganhe conosco.</h2>
                            <StyledInput mask={null} placeholder="Insira aqui seu e-mail" tabIndex={-1} />
            <Button color="fourth" tabIndex={-1}>Enviar</Button>
            <Dialog open={open} onClose={handleClose} aria-labelledby="parceiro-dialog-title">
        <DialogTitle id="parceiro-dialog-title">Inscreva-se</DialogTitle>
        <DialogContent>
          <DialogContentText>
          Você receberá um email para criação de senha e completar seu perfil
          Iremos entrar em contato com você para confirmar suas informações.
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Email @ "
            type="email"
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClose} color="primary">
            Enviar
          </Button>
        </DialogActions>
      </Dialog>
</div> 
           </div>   
                <br/>
                </p>
            </main>
        </Fragment >

    );
}
export default Index; 